.gpt3__cta {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;

	padding: 2rem;
	margin: 4rem;

	background: var(--gradient-bar);
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 1rem;
}

.gpt3__cta-content h3 {
	font-family: var(--font-family);
	font-weight: 800;
	font-size: 24px;
	line-height: 45px;
	color: #000000;
}

.gpt3__cta-content p {
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 12px;
	line-height: 30px;
	/* or 250% */
	color: #0e0e0e;
}

.gpt3__cta-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 2rem;
}

.gpt3__cta-btn button {
	font-family: var(--font-family);
	font-weight: 700;
	font-size: 18px;
	line-height: 30px;
	color: #ffffff;

	background: #000000;
	padding: 0.5rem 1rem;
	border: none;
	outline: none;
	min-width: 150px;
	border-radius: 40px;

    cursor: pointer;
}

@media screen and (max-width: 650px) {
    .gpt3__cta {
        flex-direction: column;
        text-align: center;
    }

    .gpt3__cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 4rem 2rem;
    }

    .gpt3__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .gpt3__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}